<template>
    <div class="login-container">
        <div class="login-card">
            <h2>–í—Ö–æ–¥ –≤ —Å–∏—Å—Ç–µ–º—É</h2>
            <form @submit.prevent="handleLogin">
                <div class="form-group">
                    <input v-model="username" type="text" placeholder="–ò–º—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è" required>
                </div>
                <div class="form-group">
                    <input v-model="password" type="password" placeholder="–ü–∞—Ä–æ–ª—å" required>
                </div>
                <button type="submit" :disabled="loading">–í–æ–π—Ç–∏</button>
                <p class="register-link">
                    –ù–µ—Ç –∞–∫–∫–∞—É–Ω—Ç–∞? <a href="#" @click.prevent="$router.push('/register')">–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</a>
                </p>
            </form>
            <p v-if="error" class="error">{{ error }}</p>
        </div>
    </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
    name: 'UserLogin',
    data() {
        return {
            username: '',
            password: '',
            loading: false,
            error: ''
        }
    },
    methods: {
        ...mapActions(['login']),
        async handleLogin() {
            this.loading = true
            this.error = ''

            try {
                await this.login({
                    username: this.username,
                    password: this.password
                })
                this.$router.push('/')
            } catch (error) {
                this.error = '–ù–µ–≤–µ—Ä–Ω–æ–µ –∏–º—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –∏–ª–∏ –ø–∞—Ä–æ–ª—å'
            } finally {
                this.loading = false
            }
        }
    }
}
</script>

