<template>
    <div class="register-container">
        <div class="register-card">
            <h2>–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</h2>
            <form @submit.prevent="handleRegister">
                <div class="form-group">
                    <input v-model="username" type="text" placeholder="–ò–º—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è" required>
                </div>
                <div class="form-group">
                    <input v-model="password" type="password" placeholder="–ü–∞—Ä–æ–ª—å" required>
                </div>
                <div class="form-group">
                    <input v-model="confirmPassword" type="password" placeholder="–ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å" required>
                </div>
                <button type="submit" :disabled="loading">–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</button>
                <p class="login-link">
                    –£–∂–µ –µ—Å—Ç—å –∞–∫–∫–∞—É–Ω—Ç? <a href="#" @click.prevent="$router.push('/login')">–í–æ–π—Ç–∏</a>
                </p>
            </form>
            <p v-if="error" class="error">{{ error }}</p>
        </div>
    </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
    name: 'UserRegister',
    data() {
        return {
            username: '',
            password: '',
            confirmPassword: '',
            loading: false,
            error: ''
        }
    },
    methods: {
        ...mapActions(['register', 'login']), // –î–æ–±–∞–≤–ª—è–µ–º –¥–µ–π—Å—Ç–≤–∏–µ login
        async handleRegister() {
            if (this.password !== this.confirmPassword) {
                this.error = '–ü–∞—Ä–æ–ª–∏ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç'
                return
            }

            this.loading = true
            this.error = ''

            try {
                // –°–Ω–∞—á–∞–ª–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä—É–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
                await this.register({
                    username: this.username,
                    password: this.password
                })

                // –ó–∞—Ç–µ–º –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ –≤—ã–ø–æ–ª–Ω—è–µ–º –≤—Ö–æ–¥
                await this.login({
                    username: this.username,
                    password: this.password
                })

                // –ü–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª—è–µ–º –Ω–∞ –≥–ª–∞–≤–Ω—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É
                this.$router.push('/')
            } catch (error) {
                this.error = '–ù–µ —É–¥–∞–ª–æ—Å—å —Å–æ–∑–¥–∞—Ç—å –∞–∫–∫–∞—É–Ω—Ç. –í–æ–∑–º–æ–∂–Ω–æ, –∏–º—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è —É–∂–µ –∑–∞–Ω—è—Ç–æ.'
            } finally {
                this.loading = false
            }
        }
    }
}
</script>

